body {
  background-color: #3b4f6c;
}

.page .title {
  border-bottom: 6px solid #3b4f6c;
}

.card {
  border: 1.5px dashed gray;
  border-radius: 4px;
  margin: 1rem 0;
  padding: 0 1.5rem 1rem;
  transition: all .5s ease-in-out;
}

.card:hover {
  box-shadow: 0 3px 10px #0003;
}

h3.lighter {
  color: #000;
  font: lighter;
}

h3 a {
  color: #000;
  border-bottom: 2px dashed #6987b5;
}

h3 a:hover {
  border-bottom: 4px solid #6987b5;
}

